section {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 850px;
  min-height: 100vh;

  padding: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #161616;
}
section .circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #e51e2a;
  clip-path: circle(70% at right -20%);
}
header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 40px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.logo {
  position: relative;
  max-width: 250px;
}
.navigation {
  position: relative;
  display: flex;
}
.navigation li {
  list-style: none;
}
.navigation li a {
  display: inline-block;
  color: #fff;
  font-weight: 500;
  text-decoration: none;
  margin-left: 40px;
  font-size: 18px;
}
.content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.content .textBox {
  position: relative;
  max-width: 600px;
  margin-right: 20px;
}
.content .textBox h2 {
  color: #fff;
  font-size: 50px;
  margin-bottom: 10px;
  line-height: 1.4em;
  font-weight: 700;
}
.content .textBox p {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  color: #fff;
  font-size: 16px;
}
.content .textBox a {
  display: inline-block;
  margin-top: 20px;
  padding: 8px 20px;
  background: #e51e2a;
  color: #fff;
  border-radius: 40px;
  font-weight: 500;
  letter-spacing: 1px;
  text-decoration: none;
}
.sci {
  position: absolute;
  bottom: 40px;
  left: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sci li {
  list-style: none;
}
.sci li a {
  display: inline-block;
  margin-left: 15px;
  background: #222;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  transition: 0.2s ease-in-out;
}
.sci li a:hover {
  background: #e51e2a;
  transform: translateY(-10px);
}
.sci li a img {
  filter: invert(1);
  transform: scale(0.5);
}

.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 180px;
  height: 460px;
  display: flex;
  justify-content: center;
}

.swiper-slide img {
  position: absolute;
  bottom: 0;
  max-height: 100%;
}
.imgBox {
  width: 500px;
  display: flex;
  justify-content: flex-end;
}
.imgBox img {
  max-width: 150px;
}
.swiper-3d .swiper-slide-shadow-left,
.swiper-3d .swiper-slide-shadow-right {
  background-image: none;
}
.navigation li a:hover {
  transition: all 0.2s ease-in-out;
  color: rgb(164, 167, 167);
}
.content .textBox a:hover {
  transition: all 0.2s ease-in-out;
  background: rgb(241, 72, 72);
  color: rgb(164, 167, 167);
}
@media (max-width: 991px) {
  header {
    padding: 40px;
    padding-top: 0px;
  }
  section {
    padding: 180px 40px 150px;
  }
  .sci {
    left: 40px;
  }
  .content {
    flex-direction: column;
  }
  .content .textBox,
  .content .imgBox {
    max-width: 100%;
  }
  .swiper-slide {
    height: 300px;
  }
  header .navigation {
    display: none;
  }
  header .navigation.active {
    position: fixed;
    display: flex;
    padding-top: 150px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #e51e2a;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    z-index: 10;
  }
  .toggle {
    position: relative;
    width: 30px;
    height: 30px;
    background: url(../img/menu.webp);
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    z-index: 1000;
  }
  .toggle.active {
    position: fixed;
    right: 40px;
    background: url(../img/c.webp);
    background-size: 28px;
    background-repeat: no-repeat;
    background-position: center;
  }

  .navigation li a {
    display: inline-block;
    color: #fff;
    font-weight: 300;
    text-decoration: none;
    margin-left: 0px;
    margin: 10px 0;
    font-size: 2em;
  }
  .navigation li {
    width: 320px;
    padding: 8px 12px;
    transition: all 0.2s ease-in-out;
  }
  .navigation li:hover {
    background: rgba(0, 0, 0, 0.2);
  }
  .toggle.active .body {
    overflow: hidden;
  }
}
@media (max-width: 570px) {
  .content .textBox h2 {
    font-size: 30px;
  }
  section {
    padding: 180px 15px 150px;
  }
  .logo {
    width: 200px;
  }
  .navigation li {
    width: 260px;
  }
  header .navigation.active {
    padding-top: 120px;
  }
}
